.firstTimeGroupFilterPage .banner-img {
  flex-shrink: 0;
  width: 65px;
  box-shadow: 0 3px 5px #0000003b;
  height: 65px;
  border-radius: 50%;
  background-size: 34%;
  margin-right: 20px; }

.firstTimeGroupFilterPage .gtitle {
  color: #010947 !important;
  font-size: 1rem;
  margin-bottom: 5px !important; }

.firstTimeGroupFilterPage .gsubtitle {
  color: #8d8d8d;
  font-size: 0.8rem;
  margin-bottom: 15px; }

.firstTimeGroupFilterPage .groupsContainer {
