  flex-direction: column;
  background-color: #fff;
  padding: 20px;
  margin-top: 50px; }
  .firstTimeGroupFilterPage .groupsContainer .heading {
    display: flex;
    align-items: center; }
    .firstTimeGroupFilterPage .groupsContainer .heading p {
      margin-bottom: 0; }
    .firstTimeGroupFilterPage .groupsContainer .heading span {
