      left: -5%;
      cursor: pointer; }
    .firstTimeGroupFilterPage .groupsContainer .categoriesBlogArea .rightArrow {
      z-index: 2;
      cursor: pointer;
      position: absolute;
      top: 40%;
      right: -5%; }
    .firstTimeGroupFilterPage .groupsContainer .categoriesBlogArea .categoryBlog {
      position: relative; }
      .firstTimeGroupFilterPage .groupsContainer .categoriesBlogArea .categoryBlog span {
        position: absolute;
        bottom: 5%;
        left: 8%;
        color: #fff;
        font-family: ProximaNova-Regular;
        font-style: normal;
        font-weight: bold;
        font-size: 14px; }
  .firstTimeGroupFilterPage .groupsContainer .groupBlogArea {
