    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-top: 20px; }
    .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .leftArrow {
      position: absolute;
      cursor: pointer;
      top: 36%;
      left: -5%;
      z-index: 100; }
    .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .rightArrow {
      position: absolute;
      cursor: pointer;
      top: 36%;
      right: -5%;
      z-index: 100; }
    .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .groupBlog {
