          align-content: center; }
        .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .groupBlog .rightArea .al-r {
          align-self: flex-end;
          margin-left: auto; }
        .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .groupBlog .rightArea ul {
