          margin-left: 16px;
          margin-bottom: 0; }
        .firstTimeGroupFilterPage .groupsContainer .groupBlogArea .groupBlog .rightArea li {
          margin-right: 10px;
          list-style-type: disc;
          font-family: ProximaNova-Regular;
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 110%;
          color: #8f8f8f; }
  @media screen and (max-width: 760px) {
