    .firstTimeGroupFilterPage .groupsContainer .PagesBlogsArea {
      display: block;
      align-self: center; }
      .firstTimeGroupFilterPage .groupsContainer .PagesBlogsArea .line {
        display: none; }
      .firstTimeGroupFilterPage .groupsContainer .PagesBlogsArea .pagesBlog {
        margin-bottom: 10px; }
      .firstTimeGroupFilterPage .groupsContainer .PagesBlogsArea .blogDetails {
        display: block !important; }
    .firstTimeGroupFilterPage .groupsContainer .leftArrow,
